.button-menu {
  display: none;

  @media (max-width: $mobile-width) {
    display: block;
    position: absolute;
    top: 20px;
    right: 0;
    width: 16px;
    height: 16px;
    background-color: $white;
    background-image: url("../img/icons/minus.svg");
    background-repeat: no-repeat;
    border: none;

    &--closed {
      background-image: url("../img/icons/plus.svg");
    }
  }
}


